body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
		"Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.ReactCollapse--collapse {
	transition: height 500ms;
}

@font-face {
	font-family: "GasoekOne";
	src: url(./public/assets/fonts/GasoekOne-Regular.ttf);
}

@font-face {
	font-family: "Pretendard";
	font-weight: 900;
	font-display: swap;
	src: local("Pretendard Black"), url("./public/assets/fonts/woff2/Pretendard-Black.woff2") format("woff2"),
		url("./public/assets/fonts/woff/Pretendard-Black.woff") format("woff");
}

